.production {
  background-color: $color-backgroundlight;
  padding-top     : 127px;
  padding-bottom  : 127px;

  @media (max-width: $tablet-width-max) {
    padding-top   : 35px;
    padding-bottom: 18px;
  }

  @media (max-width: $mobile-width-max) {
    padding-bottom: 14px;
  }
}

.production h2 {
  margin-bottom: 50px;

  @media (max-width: $tablet-width-max) {
    margin-bottom: 35px;
  }
}

.production__list {
  @include no-list;
  text-align     : center;
  display        : flex;
  justify-content: space-between;
  flex-wrap      : wrap;
  width          : 96%;
  margin         : auto;

  @media (max-width: $desktop-width) {
    width: 80%;
  }

  @media (max-width: $tablet-width-max) {
    width: 84%;
  }

  @media (max-width: 950px) {
    width: 92%;
  }

  @media (max-width: $mobile-width-max) {
    flex-direction: column;
    padding       : 0;
    margin        : auto;
  }
}

.production__list li {
  margin: 0 auto;

  @media (max-width: $desktop-width) {
    margin-bottom: 29px;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 18px;
  }
}

.production__list li:nth-child(3n + 1) {
  position  : relative;
  z-index   : 1;
  margin-top: 10px;

  @media (max-width: $tablet-width-max) {
    margin-top: 0;
  }

  &::before {
    position     : absolute;
    content      : "";
    width        : 180px;
    height       : 180px;
    border-radius: 50%;
    border       : 27px solid $color-blue;
    top          : 155px;
    left         : -61px;
    z-index      : -1;

    @media (max-width: $tablet-width-max) {
      width       : 127px;
      height      : 127px;
      border-width: 18px;
      top         : 200px;
      left        : -36px;
    }

    @media (max-width: $mobile-width-max) {
      top : 203px;
      left: -25px;
    }
  }
}

.production__list li:nth-child(3n + 2) {
  position  : relative;
  margin-top: 39px;

  @media (max-width: $desktop-width) {
    margin-top: 0;
  }

  &::before {
    position: absolute;
    content : "";
    width   : 47px;
    height  : 47px;
    border  : 8px solid $color-blue;
    top     : -18px;
    left    : -18px;

    @media (max-width: $tablet-width-max) {
      display: none;
    }
  }

  &::after {
    position: absolute;
    content : "";
    width   : 47px;
    height  : 47px;
    border  : 8px solid $color-blue;
    top     : 540px;
    right   : -22px;

    @media (max-width: $tablet-width-max) {
      display: none;
    }
  }
}

.production__list li:nth-child(2n + 3) {
  position: relative;

  &::before {
    position     : absolute;
    content      : "";
    width        : 95px;
    height       : 95px;
    border-radius: 50%;
    border       : 17px solid $color-blue;
    top          : -32px;
    right        : -35px;

    @media (max-width: $tablet-width-max) {
      width       : 68px;
      height      : 68px;
      border-width: 13px;
      top         : -21px;
      right       : -24px;
    }

    @media (max-width: $mobile-width-max) {
      top  : -20px;
      right: -15px;
    }
  }
}

.production__img {
  width: 342px;

  @media (max-width: $tablet-width-max) {
    width : 270px;
    height: 300px;

    &--road {
      img {
        object-position: 0 -50px;
      }
    }

    &--montain {
      img {
        object-position: 0 -60px;
      }
    }

    &--children {
      img {
        object-position: 0 -100px;
      }
    }
  }
}

.production__img img {
  width        : 100%;
  height       : 100%;
  object-fit   : cover;
  border-radius: 10px;
}


.production__list a {
  display: block;
  cursor : pointer;

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  &:active {
    color  : $color-blue;
    opacity: 1;
  }
}

.production__list h3 {
  font-size     : 22px;
  line-height   : 34px;
  font-weight   : 600;
  letter-spacing: -0.5px;
  margin-top    : 20px;
}
