@font-face {
  font-family: "opensans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "opensans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "opensans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "opensans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "opensans", "Arial", sans-serif;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5%;
  color: #000000;
  font-weight: 400;
  background-color: #ffffff;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100vh;
}
@media (max-width: 767px) {
  body {
    font-size: 18px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 30px;
    margin: 0 auto;
  }
}
@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    padding: 0 35px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding: 0 14px;
  }
}

.body__js {
  overflow: hidden;
}

.header {
  position: relative;
}

.nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .nav__toggle {
    position: absolute;
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    background: url("../img/icon-menu.svg") no-repeat center;
    background-color: #0ad9c6;
    border: none;
    top: 0;
    right: 0;
    cursor: pointer;
    z-index: 100;
  }
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7);
  left: 50%;
  transform: translate(-50%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 873px;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .nav__list {
    position: relative;
    font-size: 20px;
    line-height: 28px;
    font-weight: 400;
    color: #48494d;
    flex-direction: column;
    background-color: #ffffff;
    border: none;
    width: 100%;
    padding: 70px 0 138px 0;
  }
}

@media (max-width: 1023px) {
  .nav--close .nav__list {
    display: none;
  }
}

@media (max-width: 1023px) {
  .nav--open .nav__list {
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 90;
  }
}

@media (max-width: 1023px) {
  .nav--open .nav__toggle {
    position: absolute;
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    background: url("../img/icon-close.svg") no-repeat center;
    background-color: #ffffff;
    top: 0;
    right: 0;
    z-index: 100;
  }
}

@media (max-width: 1023px) {
  .nav--nojs .nav__toggle {
    display: none;
  }
}

@media (max-width: 1023px) {
  .nav--nojs .nav__list {
    position: relative;
    width: 100%;
  }
}

