  position: relative;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .nav__item {
    margin-top: 30px;
    background-color: #f6f6f6;
  }
  .nav__item::before {
    position: absolute;
    content: "";
    width: 10px;
    height: 10px;
    border-top: 2px solid rgba(44, 46, 63, 0.5);
    border-right: 2px solid rgba(44, 46, 63, 0.5);
    transform: rotate(45deg);
    right: 25px;
    bottom: 25px;
  }
}

.nav__link {
  display: block;
  padding-top: 21px;
  padding-bottom: 9px;
}
@media (max-width: 1023px) {
