  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 40px auto;
}
@media (max-width: 1023px) {
  .footer__list {
    margin-top: 22px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    flex-direction: column;
    align-items: center;
    margin-top: 32px;
    margin-bottom: 30px;
  }
}

.footer__item {
  margin-bottom: 10px;
}

.footer__item:not(:last-child) {
  margin-right: 80px;
}
@media (max-width: 767px) {
  .footer__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 25px;
  }
}

.footer__link {
  position: relative;
  padding: 10px 10px 10px 39px;
}
@media (max-width: 767px) {
  .footer__link {
    padding-right: 0;
  }
}
.footer__link::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-position: left center;
  left: 0;
}
.footer__link--mail::before {
  width: 26px;
  height: 20px;
  background-image: url("../img/icon-mail.svg");
}
.footer__link--phone::before {
  width: 21px;
  height: 22px;
  background-image: url("../img/icon-phone.svg");
}
.footer__link:hover, .footer__link:focus {
  color: #000000;
}
.footer__link:active {
  color: #0ad9c6;
  opacity: 1;
}